<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/img/logo.png"> -->
    <button @click="gorouter()">pdf</button>
    <button @click="gorouter('recorderDemo')">shipin</button>
    <button @click="gorouter('luyin')"></button>
    <router-view />
    <button @click="handleCLick('mv1')">mv1</button>
    <button @click="handleCLick('mv2')">mv2</button>
    <button @click="handleCLick('mv3')">mv3</button>
    <button @click="handleCLick('mv4')">mv4</button>
    <ns-video v-if="showMv" :src="src"></ns-video>
  </div>  
</template>

<script>
import nsVideo from '@/components/nsVideo'
import mv1 from '@/assets/mv/1.mp4'
import mv2 from '@/assets/mv/2.mp4'
import mv3 from '@/assets/mv/3.mp4'
import mv4 from '@/assets/mv/4.mp4'
export default {
  name: 'App',
  data(){
    return{
      mv1,
      mv2,
      mv3,
      mv4,
      src: '',
      showMv: true,
    }
  },
  components: {
    nsVideo,
  },
  created(){
    this.src = this.mv1;
  },
  mounted(){},
  methods: {
    gorouter(v) {
      if(v) {
        this.$router.push({name: v});
      }else {
        this.$router.push({name: 'pdf'});
      }
    },
    handleCLick(mvName) {
      this.showMv = false;
      this.src = this[mvName]
      setTimeout(() => {
        this.showMv = true;
      }, 800)
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
